dapi-spinner {
  margin: auto;
  display: block;
  width: 50px;
  height: 50px;
  border: 3px solid rgba(0,0,0,.3);
  border-radius: 50%;
  border-top-color: #fff;
  animation: dapi-spin 1s ease-in-out infinite;
}

dapi-spinner[inline] {
  margin: 0;
  display: inline-block;
  height: 12px;
  width: 12px;
}

dapi-spinner[page] {
  margin: 50% auto;
}

@keyframes dapi-spin {
  to { transform: rotate(360deg); }
}
